export default {
  data: function data() {
    return {};
  },
  computed: {
    getRoute: function getRoute() {
      return this.$route.matched;
    }
  },
  mounted: function mounted() {
    console.log('路径', this.$route);
  },
  methods: {
    navActive: function navActive() {
      var tag = {
        name: '首页',
        path: '/index'
      };
      this.$store.commit('getActiveTag', tag);
      this.$router.push(tag.path);
    }
  }
