import path from 'path';
export default {
  name: 'MenuItem',
  props: ['route', 'basepath'],
  data: function data() {
    return {
      tagsList: [],
      newList: [],
      tagsPath: '',
      saveOff: true
    };
  },
  mounted: function mounted() {// console.log('传递的路由信息', this.route)
  },
  methods: {
    // routepath 为当前菜单的path值
    // getpath: 拼接 当前菜单的上一级菜单的path 和 当前菜单的path
    getPath: function getPath(routePath) {
      return path.resolve(this.basepath, routePath);
    },
    tagsItem: function tagsItem(e) {
      var _this = this;

      console.log('获取当前导航菜单', e);

      if (e && e.name == 'index') {
        this.tagsPath = '/';
      } else {
        this.tagsPath = this.basepath + '/' + e.path;
      }

      this.tagsList = this.$store.state.tagsList;
      this.newList = {
        name: e.meta.title,
        path: this.tagsPath
      }; //当前的激活菜单

      this.saveOff = true;
      this.tagsList.forEach(function (item) {
        if (item.name == e.meta.title) {
          //当前的激活菜单是否已经存在tag激活标签，存在则不存入tags标签数组
          _this.saveOff = false;
        }
      });

      if (this.saveOff) {
        //当前菜单未打开过，不存在tag激活标签，则存入tags标签数组
        this.$store.commit('getTagsList', this.newList);
      } // 更新tag激活标签


      this.$store.commit('getActiveTag', this.newList); // console.log('33', this.$store.state.activeTag)
    }
  }
};